
function* Bubble(arr) {
    
    var swapping;
    do {
        swapping = false;
        for (let i=1; i<arr.length; i++) {
            if (arr[i] > arr[i-1]) {
                arr = swap(arr, i, i-1);
                swapping =  true;
                yield arr;
            }
        }
    } while (swapping);
}

function swap(a, i, j) {
    var t = a[i];
    a[i] = a[j];
    a[j] = t;
    return a;
}




export default Bubble;