

function Travelling_Sales(nodes) {
    var unvisited = [];
    var visited = [];

    nodes.sort((elem1, elem2) => {
        return elem2["x"] - elem1["x"];
    })

    if (nodes.length === 0) {
        return nodes;
    }

    for (let i=0; i<nodes.length; i++) {
        unvisited.push(nodes[i]);
    }

    visited.push(unvisited.pop());
    visited[visited.length-1]["color"] = "green";

    while (unvisited.length > 0) {
        var record = 100000;
        var winner = 0;
        let d;
        var current = visited[visited.length-1];
        for (let i=0; i<unvisited.length; i++) {
            d = Math.sqrt( Math.pow(current["x"] - unvisited[i]["x"], 2) + Math.pow(current["y"] - unvisited[i]["y"], 2));
            if (d < record) {
                record = d;
                winner = i;
            }
        }
        current["connections"].push(unvisited[winner]);
        visited.push(unvisited[winner])
        unvisited[winner]["color"] = "white";
        unvisited.splice(winner, 1);
    }
    visited[visited.length-1]["color"] = "red";
    return visited;
}

export default Travelling_Sales;