import React from "react";
import p5 from "p5";

import "./algorithm_selection.css";

import SortingAlgorithm from "../algorithms/sorting/main"

class Sort extends React.Component {
    constructor(props) {
        super(props);
        this.sort_sketch = React.createRef();
    }

    componentDidMount() {
        this.p5 = new p5(SortingAlgorithm, this.sort_sketch.current);
        this.p5.algorithm = null;
    }

    changeAlgorithm(algorithm) {
        this.p5.refresh = true;
        this.p5.algorithm = algorithm;
    }

    render() {
        return (
            <div>
                <div className="sketch" ref={this.sort_sketch}>
                </div>
                <header className="alogrithms">
                    <button onClick={() => this.changeAlgorithm("bubble")}>Bubble Sort</button>
                    <button onClick={() => this.changeAlgorithm("quicksort")}>Quick Sort</button>
                    <div className="alogrithms-right">
                        <button onClick={() => {this.p5.speed = this.p5.speed * 2}}>x2</button>
                        <button onClick={() => {this.p5.speed = Math.max(1, this.p5.speed * 0.5)}}>x.5</button>
                        <button onClick={() => {this.p5.smallerArray = true}}>Shrink</button>
                        <button onClick={() => {this.p5.largerArray = true}}>Grow</button>
                        <button onClick={() => {this.p5.reset = true}}>Reset</button>
                    </div>
                </header>
            </div>
        )
    }
}

export default Sort;