import { Bubble, QuickSort } from "../sorting"

const SortingAlgorithm = p => {

    const change = 10;
	const minY = p.windowHeight * 0.12;
    const maxY = p.windowHeight * 0.89;
    const widthMargin = p.windowWidth * 0.1
    const width = p.windowWidth - widthMargin * 2;
    p.speed = 8
    let count = 0;
    const defaultValue = 100;
    var arr = createRandomArray(defaultValue);
    
    var algorithm_in_process;
  
    p.setup = () => {
        p.createCanvas(p.windowWidth, p.windowHeight);
        execute();
    }
  
    p.draw = () => {
		p.background(0);

		if (p.reset) {
            arr = createRandomArray(defaultValue);
            algorithm_in_process = null;
			p.reset = false;
		}

		if (p.largerArray) {
            arr = createRandomArray(arr.length+change);
			p.largerArray = false;
        }
        if (p.smallerArray) {
            arr = createRandomArray(Math.max(40, arr.length-change));
			p.smallerArray = false;
        }

        if (p.refresh) {
            if (algorithm_in_process != null) {
                arr = createRandomArray(arr.length);
            }
            execute();
            p.refresh = false;
        }
        
        var scale = width/arr.length
        p.strokeWeight(scale);
        p.stroke(255);
        for (let i=0; i<arr.length; i++) {
            var x = i*scale+widthMargin;
            p.line(x, arr[i]+minY, x, maxY);
        }

        var temp;
        if (algorithm_in_process != null) {

            count += p.speed
            while (count>=1) {
                temp = algorithm_in_process.next().value;
                if (temp != null) {
                    arr = temp;
                } else {
                    algorithm_in_process = null;
                    break;
                }
                count--;
            }


        }


    }
    
    function createRandomArray(size) {
        arr = [];
        for (let i=0; i<size; i++) {
            arr.push(Math.floor(Math.random() * (maxY-minY)));
        }
        return arr;
    }

	const execute = () => {

		if (p.algorithm === "bubble") {
			algorithm_in_process = Bubble(arr);
        } else if (p.algorithm === "quicksort") {
            algorithm_in_process = QuickSort(arr, 0, arr.length-1);
        }

	}
}

export default SortingAlgorithm;